import range from "@/util/range";
import get from "@/util/get";
import getNextOpenStatusChange from "@/util/getNextOpenStatusChange";
import { DEFAULT_MINIMUM_PREORDER_DELAY } from "@/util/constants";
import i18n from "@/i18n";
import {
  nDaysAhead,
  filterByDay,
  getDecimalHours,
  convertTimeStrToDate,
  convertDateToStr,
  decimalHoursNow,
} from "@/util/openingHours";

function isTodaySelected(selectedDay) {
  return selectedDay === 0;
}

function nSlotsPerHour(n = 2) {
  return (start, end) => {
    const decimalStart = getDecimalHours(start);
    const decimalEnd = getDecimalHours(end);
    const arr = range(n * (decimalEnd - decimalStart), 1)
      .map(i => decimalStart + i / n)
      .map(decimalTime => {
        const hh = `${Math.floor(decimalTime)}`.padStart(2, "0");
        const mm = `${(60 * (decimalTime % 1)).toFixed(0)}`.padStart(2, "0");
        return `${hh}:${mm}`;
      });
    return arr;
  };
}

export const isOpenOn = ({ whichDay, openingHours }) => {
  const [day = []] = filterByDay(nDaysAhead(whichDay))(openingHours);
  return day.length && day[1] !== "closed";
};

export const nDaysAHeadToString = nNextDay => {
  switch (nNextDay) {
    case 0:
      return "today";
    case 1:
      return "tomorrow";
    default:
      return nDaysAhead(nNextDay);
  }
};

export function getDeliveryHours({
  selectedDay,
  selectedShipment,
  openingHours,
  isOpen,
  // lastOrderTime,
}) {
  //ceil up to 0.5. Exp: minimum_preorder_delay = 130 => deliveryDelayInHours = 2.5 (not 2 or 3)
  const minimum_preorder_delay = get(
    selectedShipment,
    "minimum_preorder_delay",
    DEFAULT_MINIMUM_PREORDER_DELAY
  );
  const deliveryDelayInHours = Math.ceil((minimum_preorder_delay * 2) / 60) / 2;

  const firstOption =
    isTodaySelected(selectedDay) && isOpen
      ? [
          {
            label: i18n.t("asap"),
            value: "NULL",
          },
        ]
      : [];

  const selectedDayOpenHours = filterByDay(nDaysAhead(selectedDay))(
    openingHours
  );

  let availableTimeStrList = selectedDayOpenHours.reduce(
    (availableTimeStrList, [, start, end]) => {
      return [
        ...availableTimeStrList,
        ...nSlotsPerHour(2)(start, end).filter(
          slot =>
            !isTodaySelected(selectedDay) ||
            !isOpen ||
            getDecimalHours(slot) > decimalHoursNow() + deliveryDelayInHours
        ),
        // getDecimalHours(slot) > decimalHoursNow() + deliveryDelayInHours
      ];
    },
    []
  );

  // const preorder_time_window = get(selectedShipment, "preorder_time_window");
  const preorder_time_window = 0;
  if (preorder_time_window && availableTimeStrList.length) {
    const [, openTimeStr, closeTimeStr] = get(selectedDayOpenHours, "0", []);

    const availableTimeSlots = getAvailableTimeSlots({
      openTimeStr,
      closeTimeStr,
      availableTimeStrList,
      preorder_time_window,
    });

    return [
      ...firstOption,
      ...availableTimeSlots.map(({ startTimeStr, endTimeStr }) => ({
        label: `${startTimeStr} - ${endTimeStr}`,
        value: startTimeStr,
      })),
    ];
  }

  return (
    [
      ...firstOption,
      ...availableTimeStrList.map(timeStr => ({
        label: timeStr,
        value: timeStr,
      })),
    ] || []
  );
}

export function getAvailableTimeSlots({
  openTimeStr,
  closeTimeStr,
  availableTimeStrList,
  preorder_time_window,
}) {
  const allTimeSlots = getAllTimeSlots({
    openTimeStr,
    closeTimeStr,
    preorder_time_window,
  });
  const filteredTimeSlotInRange = filterTimeSlotInRange({
    beginTimeStr: availableTimeStrList[0],
    allTimeSlots,
  });
  return filteredTimeSlotInRange;
}

export function getAllTimeSlots({
  openTimeStr,
  closeTimeStr,
  preorder_time_window,
}) {
  // build time slots base on open time and close time
  const result = [];

  if (![openTimeStr, closeTimeStr].every(Boolean)) return result;

  const preorderTimeInMs = preorder_time_window * 60000;
  const closeTimeInDate = convertTimeStrToDate(closeTimeStr);
  let anchorTime = convertTimeStrToDate(openTimeStr);
  do {
    const startTimeStr = convertDateToStr(anchorTime);
    anchorTime = new Date(+anchorTime + preorderTimeInMs);
    const endTimeStr = convertDateToStr(anchorTime);
    result.push({
      startTimeStr,
      endTimeStr,
    });
  } while (+anchorTime + preorderTimeInMs <= +closeTimeInDate);

  return result;
}

export function filterTimeSlotInRange({ beginTimeStr, allTimeSlots }) {
  const withinSlotIndex = allTimeSlots.findIndex(
    ({ startTimeStr, endTimeStr }) =>
      getDecimalHours(startTimeStr) < getDecimalHours(beginTimeStr) &&
      getDecimalHours(beginTimeStr) <= getDecimalHours(endTimeStr)
  );

  if (withinSlotIndex === -1) return [];

  return allTimeSlots.slice(withinSlotIndex + 1);
}

export const getDayOptions = ({
  selectedShipment,
  openingHours,
  isOpen,
  lastOrderTime,
}) => {
  if (!selectedShipment || !openingHours) return [];

  const selectDayOptions = range(7)
    .filter(dayValue => isOpenOn({ whichDay: dayValue, openingHours }))
    .map(value => ({
      label: i18n.t(nDaysAHeadToString(value)),
      value,
    }));

  const selectedDay = 0;

  // const result = {
  //   selectDayOptions,
  //   selectedDay,
  // }

  const todayAvailableDeliveryHours = getDeliveryHours({
    selectedDay,
    selectedShipment,
    openingHours,
    isOpen,
    lastOrderTime,
  });

  if (todayAvailableDeliveryHours && todayAvailableDeliveryHours.length <= 1) {
    // no time options for today ([] || 'asap')
    // const selectedDayOpenTime = filterByDay(nDaysAhead(selectedDay))(
    //   openingHours
    // );
    const nextStatusChange = getNextOpenStatusChange({ openingHours });
    const { nextChangeDate } = nextStatusChange;

    // const [, , closeTimeStr] = get(
    //   selectedDayOpenTime,
    //   `${selectedDayOpenTime.length - 1}`,
    //   []
    // );
    // if (!closeTimeStr) return selectDayOptions;

    const haveCloseDate = nextChangeDate ? nextChangeDate : isOpen;
    const isAvailableForInstantOrder =
      haveCloseDate && +new Date() < +nextChangeDate - lastOrderTime * 60000;

    if (!isAvailableForInstantOrder) {
      selectDayOptions.shift(1);
    }
  }

  return selectDayOptions;
};
