<template>
  <div class="add-note-to-seller">
    <CheckBox
      class="add-note-to-seller__checkbox"
      :checked="isMessageAreaVisible"
      :onChange="toggleMessageArea"
    >{{ $t("addNoteToSeller") }}</CheckBox>
    <textarea
      v-if="isMessageAreaVisible"
      class="add-note-to-seller__note-area"
      name="noteToSeller"
      rows="6"
      @input="onNoteChange"
    ></textarea>
  </div>
</template>

<i18n>
{
  "is": {
    "addNoteToSeller": "Skilaboð til seljanda"
  },
  "en": {
    "addNoteToSeller": "Add note to the seller"
  }
}
</i18n>

<script>
import CheckBox from "@/components/CheckBox";
import { addNoteToSeller } from "@/services/cart";
export default {
  name: "AddNoteToSeller",
  components: {
    CheckBox,
  },
  props: {
    storeId: Number,
  },
  data: function() {
    return {
      isMessageAreaVisible: false,
    };
  },
  methods: {
    toggleMessageArea(e) {
      const { deboundAddNote } = this;
      this.isMessageAreaVisible = e.target.checked;
      if (!e.target.checked) {
        deboundAddNote("");
      }
    },
    deboundAddNote(note) {
      const { storeId } = this;
      clearTimeout(this.deboundAddNoteTimer);
      this.deboundAddNoteTimer = setTimeout(
        () => addNoteToSeller({ storeId, note }),
        500
      );
    },
    onNoteChange(e) {
      const { deboundAddNote } = this;
      const value = e.target.value;
      deboundAddNote(value);
    },
  },
};
</script>

<style lang="scss" scoped>
.add-note-to-seller__note-area {
  width: 100%;
  border-radius: 4px;
  border: 1px solid #dadada;
  outline: none;
  padding: 10px;
  margin-top: 5px;
  font-size: 16px;
}
</style>
