<template>
  <!-- Payment section of the checkout page -->
  <div v-if="paymentMethods">
    <slot />
    <div v-if="isExpanded">
      <div
        v-for="method in options"
        :key="method"
        v-show="isMethodVisible(method)"
      >
        <input
          v-if="!isCheckbox(method)"
          type="radio"
          v-model="selected"
          :value="method"
          :id="method"
        />
        <input
          v-else
          type="checkbox"
          v-model="isSelectedPartialCredit"
          :id="method"
        />
        <label class="payment-methods" :for="method">{{
          $t(method, { creditISK, grandTotalISK, last4, last4New })
        }}</label>
        <div v-if="equalsSelected('borgunccNew', method)">
          <div>
            <label
              v-for="(label, value) in creditCardTypes"
              :key="value"
              :class="`cc-type-${value} card-type`"
            >
              <input type="radio" v-model="newCard.type" :value="value" />
            </label>
          </div>
          <div class="card-number-box">
            <input
              class="card-number cc-number"
              :class="ccImage()"
              name="cardnumber"
              autocomplete="cc-number"
              v-model="newCard.number"
              :placeholder="$t('newCardNumber')"
              @keydown="event => numberInputLengthN(16)(event, newCard.number)"
            />
            <select
              class="card-number short cc-mm"
              v-model="newCard.expirationMonth"
              name="cc-exp-month"
              autocomplete="cc-exp-month"
            >
              >
              <option class="firstOption" value>{{ $t("mm") }}</option>
              <option
                v-for="month in range(12, 1)"
                :value="month"
                :key="month"
                >{{ month | padZeros }}</option
              >
            </select>
            <select
              class="card-number short cc-yy"
              v-model="newCard.expirationYear"
              name="cc-exp-year"
              autocomplete="cc-exp-year"
            >
              >
              <option class="firstOption" value>{{ $t("yy") }}</option>
              <option v-for="year in ccYearRange" :value="year" :key="year">{{
                year
              }}</option>
            </select>

            <BaseButton
              v-if="newCard.verificationURL"
              class="primary verification-link"
              @click.native="open3DVerificationModal"
            >
              {{ $t("verifyYourCard") }}
            </BaseButton>
          </div>
        </div>
        <span
          v-if="equalsSelected('checkmo', method)"
          class="preserve-white-space"
          >{{ bankTransferText | trim }}</span
        >
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "pleaseFillDeliveryData": "Vinsamlegast fylltu fyrst nauðsynleg afhendingargögn",
    "customercredit": "Inneign ({creditISK})",
    "borguncc": "Greiðslukort {last4}",
    "borgunccNew": "Nýtt greiðslukort {last4New}",
    "checkmo": "Millifærsla",
    "total": "Samtals",
    "newCardNumber": "Kortanúmer",
    "yy": "Ár",
    "mm": "Mán",
    "free": "Ókeypis!",
    "verifyYourCard": "Click here to verify your card",
    "multipayment": "Greiðsludreifing"
  },
  "en": {
    "pleaseFillDeliveryData": "Please fill all required delivery data first",
    "customercredit": "Internal credit ({creditISK})",
    "borguncc": "Credit/Debit card {last4}",
    "borgunccNew": "New credit/debit card {last4New}",
    "checkmo": "Bank transfer",
    "total": "Total",
    "newCardNumber": "Card number",
    "yy": "Year",
    "mm": "Month",
    "free": "Free of charge!",
    "verifyYourCard": "Click here to verify your card",
    "multipayment": "Multipayment"
  }
}
</i18n>

<script>
import putCheckout from "@/util/putCheckout";
import BaseButton from "@/components/BaseButton";
import range from "@/util/range";
import get from "@/util/get";
import execWithLoading from "@/util/execWithLoading";
import { mapGetters, mapActions } from "vuex";
import { MODAL_KEY_3DS_VERIFICATION } from "@/store/modalModule";
import { LOADING_KEY_ADDING_PAYMENT_METHOD } from "@/store/loadingModule";

const initNewCartData = {
  number: "",
  type: "",
  expirationYear: "",
  expirationMonth: "",
  verificationURL: "",
};
export default {
  name: "CheckoutPayment",
  components: {
    BaseButton,
  },
  props: {
    isExpanded: Boolean,
  },
  data() {
    return {
      newCard: { ...initNewCartData },
      selected: "",
      isSelectedPartialCredit: false,
      paymentMethods: {},
      creditCardTypes: {
        VI: "Visa Credit",
        MC: "MasterCard Credit",
        AE: "American Express",
        VE: "Visa Electron",
        SM: "Maestro Debit",
        DM: "MasterCard Debit",
      },
    };
  },
  methods: {
    ...mapActions(["notification", "toggleModal"]),
    ccImage() {
      var str = this.newCard.number;
      var validCardRegexp = /^(?:(4[0-9]{12}(?:[0-9]{3})?)|(5[1-5][0-9]{14})|(6(?:011|5[0-9]{2})[0-9]{12})|(3[47][0-9]{13})|(3(?:0[0-5]|[68][0-9])[0-9]{11})|((?:2131|1800|35[0-9]{3})[0-9]{11}))$/;
      var visaRegEx = /^(?:4[0-9]{12}(?:[0-9]{3})?)$/;
      var mastercardRegEx = /^(?:5[1-5][0-9]{14})$/;

      if (validCardRegexp.test(str)) {
        if (visaRegEx.test(str)) {
          return "VISA";
        } else if (mastercardRegEx.test(str)) {
          return "MC";
        } else return false;
      } else {
        return false;
      }
    },
    range,
    isCheckbox(method) {
      return method === "customercredit" && this.credit < this.grandTotal;
    },
    isMethodVisible(method) {
      switch (method) {
        case "customercredit":
          return !!this.credit;
        case "checkmo":
          return this.isCartOnlyDeals;
        case "multipayment":
          return false;
        default:
          return true;
      }
    },
    equalsSelected(...args) {
      return !args.filter(arg => arg !== this.selected).length;
    },
    numberInputLengthN(n) {
      return (event, value) => {
        const isDigit = !!event.key.match(/\d/);
        const canInputMore = value.length < n;
        const isSpecial = event.key.length > 1;
        if (!((isDigit && canInputMore) || isSpecial)) {
          event.preventDefault();
        }
      };
    },
    async triggerSaveState() {
      if (this.selected === "borgunccNew" && !this.isNewCardReady) {
        this.$emit("onSelectedPaymentMethod", { method: "" });
      } else {
        this.$emit("onSelectedPaymentMethod", { method: this.selected });
      }

      switch (this.selected) {
        case "":
          return await this.saveState(false);
        case "borgunccNew":
          return this.saveState(this.isNewCardReady);
        default:
          return await this.saveState(true);
      }
    },
    async saveState(bool) {
      if (bool) {
        this.$emit("activateButton");
        const {
          type: cc_type,
          number: cc_number,
          expirationMonth: cc_exp_month,
          expirationYear,
        } = this.newCard;
        const paymentObj =
          this.selected === "borgunccNew"
            ? {
                payment: {
                  method: "borguncc",
                  use_internal_credit: this.isSelectedPartialCredit
                    ? 1
                    : undefined,
                  cc_type,
                  cc_number,
                  cc_exp_month,
                  cc_exp_year: `${expirationYear}`,
                },
              }
            : {
                payment: {
                  method: this.selected,
                  use_internal_credit:
                    this.selected === "borguncc" && this.isSelectedPartialCredit
                      ? 1
                      : undefined,
                },
              };

        const result = await execWithLoading(
          LOADING_KEY_ADDING_PAYMENT_METHOD,
          () => putCheckout(paymentObj)
        );
        if (result.status === "error") {
          this.newCard = { ...initNewCartData };
          const message =
            typeof result.message === "object"
              ? this.$t("pleaseFillDeliveryData")
              : result.message;
          this.notification({
            type: "error",
            message,
          });
          this.selected = "";
        }
        if (result.status === "success") {
          this.newCard.verificationURL = get(
            result,
            "data.centinel_data.enabled"
          )
            ? get(result, "data.centinel_data.iframe_url")
            : "";
        }
        return result;
      } else {
        this.$emit("deactivateButton");
      }
    },
    async getAvailablePaymentMethods() {
      const checkout = await fetch("/api/rest/checkout").then(response =>
        response.json()
      );
      const { payment_methods } = checkout;
      this.paymentMethods = payment_methods;
    },
    open3DVerificationModal() {
      this.toggleModal({
        modalKey: MODAL_KEY_3DS_VERIFICATION,
        verificationUrl: this.newCard.verificationURL,
        isVisible: true,
      });
    },
  },
  computed: {
    ...mapGetters(["cartItems", "putCheckoutKey"]),
    ccYearRange() {
      const now = new Date();
      return range(20, now.getFullYear());
    },
    isCartOnlyDeals() {
      return !this.cartItems.filter(
        ({ attribute_set_id }) => attribute_set_id !== 4
      ).length;
    },
    grandTotalISK() {
      return this.$options.filters.formatISK(this.grandTotal);
    },
    grandTotal() {
      return this.$store.state.quote.grand_total;
    },
    credit() {
      return parseInt(this.paymentMethods.customercredit.credit_value);
    },
    creditISK() {
      if (!this.paymentMethods.customercredit) {
        return "";
      }
      return this.$options.filters.formatISK(
        this.paymentMethods.customercredit.credit_value || 0
      );
    },
    bankTransferText() {
      return this.paymentMethods.checkmo.mailing_address;
    },
    last4New() {
      return this.newCard.number.length === 16
        ? this.newCard.number.slice(-4)
        : "";
    },
    last4() {
      return this.paymentMethods.borguncc &&
        this.paymentMethods.borguncc.card_info
        ? this.paymentMethods.borguncc.card_info.last4
        : "";
    },
    options() {
      const arr = [
        ...Object.values(this.paymentMethods)
          .filter(({ code, card_info }) => !(code === "borguncc" && !card_info))
          .map(({ code }) => code),
        "borgunccNew",
      ];
      arr.sort();
      return arr;
    },
    isNewCardReady() {
      const { number, type, expirationMonth, expirationYear } = this.newCard;
      const isReady =
        number.length === 16 &&
        type.length &&
        expirationMonth &&
        expirationYear;
      isReady && this.$emit("newCard", this.newCard);
      return isReady;
    },
  },
  watch: {
    selected() {
      this.triggerSaveState();
    },
    isSelectedPartialCredit() {
      this.triggerSaveState();
    },
    isNewCardReady(isNewCardReady) {
      if (isNewCardReady) {
        this.$emit("onSelectedPaymentMethod", { method: this.selected });
      }
      this.saveState(isNewCardReady);
    },
    putCheckoutKey() {
      this.getAvailablePaymentMethods();
    },
  },
  filters: {
    trim(str) {
      return str.trim();
    },
    padZeros(month) {
      return month.toString().padStart(2, "0");
    },
  },
  created() {
    this.getAvailablePaymentMethods();
    window.commandShortcut.reTriggerSavePaymentMethod = this.triggerSaveState;
  },
};
</script>

