<template>
  <label class="radio-btn">
    <slot />
    <input
      class="radio-btn__input"
      type="radio"
      :checked="checked"
      :name="name"
      @change="handleChange"
      :value="value"
    />
    <span class="radio-btn__checkmark" />
  </label>
</template>

<script>
export default {
  name: "RadioBtn",
  props: {
    checked: Boolean,
    name: String,
    onChange: Function,
    value: undefined,
  },
  methods: {
    handleChange(e) {
      e.target.checked = this.checked;
      this.onChange(e)
    }
  }
};
</script>

<style lang="scss" scoped>
.radio-btn {
  display: block;
  position: relative;
  padding-left: 30px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  min-height: 20px;
  font-weight: unset;
}

/* Hide the browser's default radio button */
.radio-btn__input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.radio-btn__checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: white;
  border-radius: 50%;
  border: 1px solid #8d8d8f;
}

/* On mouse-over, add a grey background color */
.radio-btn:hover .radio-btn__checkmark {
  background-color: #f4f4f4;
}

/* When the radio button is checked, add a blue background */
.radio-btn input:checked ~ .radio-btn__checkmark {
  background-color: #2abb9b;
  border: none;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.radio-btn__checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.radio-btn input:checked ~ .radio-btn__checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.radio-btn__checkmark:after {
  top: 6px;
  left: 6px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}
</style>
