<template>
  <div v-if="selectedShipment" class="delivery-time">
    <div class="delivery-time__date-time-select-container">
      <div class="delivery-time__date-select">
        <div class="delivery-time__select-wraper icon icon--calendar">
          <select @change="e => handleChangeDay(e.target.value)">
            <option
              v-for="option in selectDayOptions"
              :key="option.value"
              :selected="option.value === selectedDay"
              :value="option.value"
              >{{ option.label }}</option
            >
          </select>
          <i class="delivery-time__arrow-down-icon icon icon--arrow-down" />
        </div>
      </div>

      <div
        class="delivery-time__time-select"
        v-if="!selectedShipment.hide_preorder_time"
      >
        <div class="delivery-time__select-wraper icon icon--clock">
          <select @change="handleChangeHour">
            <option :selected="!selectedHour" value disabled hidden>{{
              $t("selectHour")
            }}</option>
            <option
              v-for="{ label, value } in deliveryHourOptions"
              :value="value"
              :selected="value === selectedHour"
              :key="value"
              >{{ label }}</option
            >
          </select>
          <i class="delivery-time__arrow-down-icon icon icon--arrow-down" />
        </div>
      </div>
    </div>
  </div>
</template>

<i18n>
  {
    "is": {
      "whenToGetFood": "Hvenær viltu fá afhent?",
      "selectHour": "Veldu tíma"
    },
    "en": {
      "whenToGetFood": "When do you want delivery?",
      "selectHour": "Select time"
    }
  }
</i18n>

<script>
import { mapGetters, mapActions } from "vuex";
import { getDayOptions, getDeliveryHours } from "@/util/deliveryTime";
import openStatus from "@/mixins/openStatus";
import get from "@/util/get";

export default {
  name: "ChangeDraftDeliveryTime",
  mixins: [openStatus],

  props: {
    id: Number,
  },
  computed: {
    ...mapGetters([
      "getSelectedShipmentByStoreId",
      "getSelectedPreOrderHourByStoreId",
      "getSelectedPreOrderDayByStoreId",
      "getPreorderTimeFromCart",
    ]),
    selectedShipment() {
      const { getSelectedShipmentByStoreId, id } = this;
      return getSelectedShipmentByStoreId(id);
    },
    selectedDay() {
      const { getSelectedPreOrderDayByStoreId, id } = this;
      return getSelectedPreOrderDayByStoreId(id);
    },
    selectedHour() {
      const { getSelectedPreOrderHourByStoreId, id } = this;
      return getSelectedPreOrderHourByStoreId(id);
    },
    selectDayOptions() {
      const { selectedShipment, openStatus, openingHours, merchant } = this;

      return getDayOptions({
        selectedShipment,
        openingHours,
        isOpen: openStatus.isOpen,
        lastOrderTime: get(merchant, "last_order_time", 30),
      });
    },
    deliveryHourOptions() {
      const {
        selectedShipment,
        openStatus,
        openingHours,
        selectedDay,
        merchant,
      } = this;

      return getDeliveryHours({
        selectedDay,
        selectedShipment,
        openingHours,
        isOpen: openStatus.isOpen,
        lastOrderTime: get(merchant, "last_order_time", 30),
      });
    },
    chosePreorderTimeFromCart() {
      const { getPreorderTimeFromCart, id } = this;
      return getPreorderTimeFromCart(id);
    },
  },
  methods: {
    ...mapActions([
      "setSelectedPreOrderHourByStore",
      "setSelectedPreOrderDayByStore",
      "addPreorderTimeToCart",
    ]),
    handleChangeDay(selectedOption) {
      const {
        setSelectedPreOrderDayByStore,
        addPreorderTimeToCart,
        id,
        deliveryHourOptions,
      } = this;
      setSelectedPreOrderDayByStore({ storeId: id, selectedOption });
      addPreorderTimeToCart({
        storeId: id,
        selectedDay: selectedOption,
        deliveryHours: deliveryHourOptions,
      });
    },
    handleChangeHour(e) {
      const {
        setSelectedPreOrderHourByStore,
        id,
        addPreorderTimeToCart,
        deliveryHourOptions,
        selectedDay,
      } = this;
      const selectedOption = e.target.value;
      setSelectedPreOrderHourByStore({ storeId: id, selectedOption });

      addPreorderTimeToCart({
        storeId: id,
        selectedDay: selectedDay,
        deliveryHours: deliveryHourOptions,
        selectedTime: selectedOption,
      });
    },
    syncSeletedPreorderTime() {
      const { id, chosePreorderTimeFromCart } = this;
      if (chosePreorderTimeFromCart) {
        this.setSelectedPreOrderDayByStore({
          storeId: id,
          selectedOption: chosePreorderTimeFromCart.selectedDay,
        });
        this.setSelectedPreOrderHourByStore({
          storeId: id,
          selectedOption: chosePreorderTimeFromCart.selectedHour,
        });
      }
    },
  },
  mounted() {
    this.syncSeletedPreorderTime();
  },
  watch: {
    selectDayOptions(newSelectDayOptions, oldSelectDayOptions) {
      if (newSelectDayOptions.length === oldSelectDayOptions.length) return;

      const selectDayValues = newSelectDayOptions.map(({ value }) => value);

      if (selectDayValues.includes(this.selectedDay)) {
        return;
      }
      // if selectDayOptions is changed because of the restaurant is going to close
      // (no option for today => the selected day should be tomorrow)
      this.handleChangeDay(get(selectDayValues, "0", 0));
    },
  },
};
</script>

