<template>
  <div class="background view-checkout__wrapper">
    <div class="container mt--40">
      <div class="header-text">{{ $t("checkout") }}</div>
      <div class="row">
        <div :class="`col-md-${cartItemsByStore.length ? '8' : '12'}`">
          <div v-for="step in steps" class="checkout-step" :key="step">
            <component
              :is="step"
              :isExpanded="expanded === step"
              @collapse="collapse"
              :expand="() => expand(step)"
              :telephone="telephone"
              @activateButton="activateButton"
              @deactivateButton="deactivateButton"
            >
              <template #edit>
                <span
                  class="customer-info-change col-xs-3"
                  v-if="canEdit(step)"
                  @click="expand(step)"
                >
                  {{ $t("edit") }}
                  <i class="icon icon--edit" />
                </span>
              </template>
            </component>
            <span v-if="expanded === step && step !== 'review'"></span>
          </div>
        </div>
        <div class="col-md-4 payment-panel" v-if="cartItemsByStore.length">
          <LoadingWrapper :loadingKeys="[LOADING_KEY_ADDING_PAYMENT_METHOD]">
            <div class="checkout-toprow">
              <div class="checkout-toprow-left">
                {{ `${countItemsInCart} ${$t("items")}` }}
              </div>
            </div>
            <div class="checkout-confirm">
              <div class="grand-total">
                <grand-total @grandTotalAmount="setGrandTotalAmount" />
              </div>
              <div class="payment-container">
                <div class="payment-headerText">{{ $t("payment") }}</div>
                <div class="checkout-payment">
                  <checkout-payment
                    :isExpanded="true"
                    @activateButton="activateButton"
                    @deactivateButton="deactivateButton"
                    @onSelectedPaymentMethod="onSelectedPaymentMethod"
                  ></checkout-payment>
                </div>
              </div>
              <CheckoutPhoneNumber
                :telephone="telephone"
                :changeTelephone="changeTelephone"
              />
              <div class="checkout-button">
                <base-button
                  :class="['primary', 'btn-block']"
                  :disabled="
                    (!isButtonActive && isShipmentFetching) ||
                      isLoading ||
                      !selectedPaymentMethod
                  "
                  @click.native="placeOrder"
                >
                  <span v-if="!isLoading && !isShipmentFetching"
                    >{{ $t("confirmAndPay") }}
                    {{ grandTotalAmount | formatISK }}</span
                  >
                  <span v-else>
                    <i class="fa fa-spinner fa-spin" />
                    &nbsp;{{ $t("processingPayment") }}
                  </span>
                </base-button>
              </div>
            </div>
          </LoadingWrapper>
        </div>
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "checkout": "Ganga frá pöntun",
    "delivery": "Heimilisfang",
    "payment": "Greiðslumáti",
    "review": "Yfirfara pöntun",
    "edit": "Breyta",
    "confirmAndPay": "Staðfesta og greiða",
    "items": "vörur",
    "processingPayment": "Augnablik"
  },
  "en": {
    "checkout": "Checkout",
    "delivery": "Delivery Address",
    "payment": "Payment Method",
    "review": "Review Order",
    "edit": "Edit",
    "confirmAndPay": "Confirm & Pay",
    "item": "items",
    "processingPayment": "Processing"
  }
}
</i18n>

<script>
import BaseButton from "@/components/BaseButton";
import CheckoutDelivery from "@/components/CheckoutDelivery";
import CheckoutPayment from "@/components/CheckoutPayment";
import CheckoutReview from "@/components/CheckoutReview";
import GrandTotal from "@/components/GrandTotal";
import CheckoutPhoneNumber from "@/components/CheckoutPhoneNumber";
import { METHOD_DELIVERY } from "@/util/constants";
import putCheckout from "@/util/putCheckout";
import removeShipmentItems from "@/util/removeShipmentItems";
import cartItemsByStore from "@/mixins/cartItemsByStore";
import { mapGetters, mapActions } from "vuex";
import LoadingWrapper from "@/components/LoadingWrapper";
import { LOADING_KEY_ADDING_PAYMENT_METHOD } from "@/store/loadingModule";

export default {
  name: "ViewCheckout",
  mixins: [cartItemsByStore],
  components: {
    BaseButton,
    delivery: CheckoutDelivery,
    review: CheckoutReview,
    CheckoutPayment,
    GrandTotal,
    CheckoutPhoneNumber,
    LoadingWrapper,
  },
  data() {
    return {
      isLoading: false,
      expanded: "review",
      isButtonActive: false,
      grandTotalAmount: 0,
      selectedPaymentMethod: "",
      telephone: "",
      LOADING_KEY_ADDING_PAYMENT_METHOD,
    };
  },
  computed: {
    ...mapGetters([
      "getCheckoutFetchingShipmentKey",
      "getLoadingState",
      "billingPhoneNumber",
    ]),
    isShipmentFetching() {
      // get all store in checkout
      const {
        getCheckoutFetchingShipmentKey,
        getLoadingState,
        cartItemsByStore,
      } = this;

      return cartItemsByStore.some(cartItem =>
        getLoadingState(getCheckoutFetchingShipmentKey(cartItem.id))
      );
    },
    countItemsInCart() {
      return removeShipmentItems(this.$store.state.cartItems).reduce(
        (sum, { cart_qty }) => sum + cart_qty,
        0
      );
    },
    steps() {
      return [this.isAddressRequired && "delivery", "review"].filter(Boolean);
    },
    nextStep() {
      const { currentIndex, steps } = this;
      return steps[currentIndex + 1];
    },
    currentIndex() {
      const { expanded, steps } = this;
      return steps.indexOf(expanded);
    },
    isAddressRequired() {
      return !!this.$store.state.cartItems
        .map(({ deal_address }) => deal_address)
        .find(n => n === METHOD_DELIVERY);
    },
  },
  methods: {
    ...mapActions(["loadCart", "setNavBarTitle"]),
    removeShipmentItems,
    setGrandTotalAmount(value) {
      this.grandTotalAmount = value;
    },
    activateButton() {
      this.isButtonActive = true;
    },
    deactivateButton() {
      this.isButtonActive = false;
    },
    placeOrder() {
      this.isLoading = true;
      putCheckout({
        place: 1,
      })
        .then(res => {
          if (res.status === "success") {
            this.$router.push({
              name: "success",
              params: {
                orderId: res.data.order_ids.main_order,
              },
            });
            this.loadCart();
          }
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    isExpanded(str) {
      return this.expanded === str;
    },
    collapse() {
      this.expanded = this.nextStep;
    },
    expand(str) {
      this.expanded = str;
    },
    canEdit(step) {
      const { currentIndex, steps } = this;
      const index = steps.indexOf(step);
      return index < currentIndex;
    },
    onSelectedPaymentMethod(paymentMethod) {
      this.selectedPaymentMethod = paymentMethod.method;
    },
    changeTelephone(phone) {
      this.telephone = phone;
    },
  },
  mounted() {
    this.setNavBarTitle({
      en: "Checkout",
      is: "Ganga frá pöntun",
    });
    this.changeTelephone(this.billingPhoneNumber);
  },
  watch: {
    billingPhoneNumber(billingPhoneNumber) {
      this.changeTelephone(billingPhoneNumber);
    },
  },
};
</script>

