import store from "@/store/store";
import { checkout } from "@/services/checkout";
import { ERROR_KEY_CHECKOUT_SCREEN } from "@/store/errorsModule";

const putCheckout = async data => {
  const result = await checkout(data);
  if (result.status === "success") {
    store.dispatch("updatePutCheckoutKey");
    return result;
  }

  if (typeof result.message !== "object") {
    store.dispatch("notification", {
      type: "error",
      message: result.message,
    });
    return result;
  }

  store.dispatch("setError", {
    errorKey: ERROR_KEY_CHECKOUT_SCREEN,
    ...result.message,
  });
  return result;
};

export default putCheckout;
