<template>
  <div class="checkout-delivery">
    <LoadingWrapper :loadingKeys="[fetchingShipmentsKey]">
      <div
        class="checkout-delivery__location-container"
        v-if="!isSelectedAPickupMethod"
      >
        <h5 class="checkout-delivery__title">{{ $t("deliveryOptionsTo") }}</h5>
        <div
          class="checkout-delivery__location"
          @click="toggleChangeAddressModal"
        >
          <LocationDisplay
            noCheckMark
            v-if="currDeliveryText"
            :info="currDeliveryText"
          />
        </div>
      </div>

      <div
        v-if="shipments && !shipments.length"
        class="checkout-delivery__no-shipment"
      >
        {{ $t("noShipments") }}
      </div>

      <div v-else class="delivery-section">
        <ul class="delivery-section__list" v-if="isDataReady">
          <li
            class="delivery-section__item"
            v-for="shipment in shipments"
            :key="shipment.entity_id"
          >
            <RadioBtn
              :checked="
                defaultShipment &&
                  defaultShipment.entity_id === shipment.entity_id
              "
              :value="shipment.entity_id"
              :name="`shipment-option-${storeId}`"
              :onChange="changeShipmentOption"
              >{{ getShipmentText(shipment) }}</RadioBtn
            >
          </li>
        </ul>
        <div class="delivery-section__plastic-bag">
          <SelectPlasticBagOption :storeId="storeId" />
        </div>
        <div class="delivery-section__time row">
          <ChangeDraftDeliveryTime :id="storeId" />
        </div>

        <AddNoteToSeller
          v-if="showNoteToSeller"
          class="delivery-section__note"
          :storeId="storeId"
        />
      </div>
    </LoadingWrapper>
  </div>
</template>
<i18n>
{
  "is": {
    "deliveryOptionsTo": "Afhendingarmátar",
    "noShipments": "Það eru engir sendingarmöguleikar á þitt heimilisfang"
  },
  "en": {
    "deliveryOptionsTo": "Delivery options to",
    "noShipments": "There is no shipments options for your address"
  }
}
</i18n>
<script>
import LocationDisplay from "@/components/DeliveryModal/LocationDisplay";
import ChangeDraftDeliveryTime from "@/components/ChangeDraftDeliveryTime";
import LoadingWrapper from "@/components/LoadingWrapper";
import SelectPlasticBagOption from "@/components/SelectPlasticBagOption";
import RadioBtn from "@/components/RadioBtn";
import AddNoteToSeller from "@/components/AddNoteToSeller";
import { mapGetters, mapActions } from "vuex";
import getShipmentTextByLanguage from "@/util/getShipmentTextByLanguage";
import { MODAL_KEY_SELECT_DELIVERY_OPTION } from "@/store/modalModule";
import get from "@/util/get";
import { METHOD_PICKUP, FULFILLMENT_METHOD } from "@/util/constants";

export default {
  name: "CheckoutDeliveryInfo",
  components: {
    LocationDisplay,
    AddNoteToSeller,
    ChangeDraftDeliveryTime,
    RadioBtn,
    LoadingWrapper,
    SelectPlasticBagOption,
  },
  props: {
    storeId: Number,
    showNoteToSeller: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      isDataReady: false,
    };
  },
  methods: {
    ...mapActions([
      "toggleLoading",
      "toggleModal",
      "addShipmentToCart",
      "setSelectedShipmentOptionByStore",
      "syncSelectedShipmentWithCart",
      "removeShipmentInCart",
    ]),
    getShipmentText(shipment) {
      return getShipmentTextByLanguage(shipment);
    },
    async changeShipmentOption(e) {
      const {
        addShipmentToCart,
        shipments,
        userAddress,
        storeId,
        toggleLoading,
        fetchingShipmentsKey,
      } = this;

      const { value } = e.target;
      toggleLoading({
        loadingKey: fetchingShipmentsKey,
        isLoading: true,
      });

      const selectedShipment = (shipments || []).find(
        s => s.entity_id === +value
      );

      try {
        const result = await addShipmentToCart({
          shipment: selectedShipment,
          shipmentPostcode: userAddress.postcode,
          storeId,
        });
      } catch (error) {
        console.log("changeShipmentOption -> error", error);
      }

      toggleLoading({
        loadingKey: fetchingShipmentsKey,
        isLoading: false,
      });
    },
    toggleChangeAddressModal() {
      const { toggleModal } = this;
      toggleModal({
        modalKey: MODAL_KEY_SELECT_DELIVERY_OPTION,
        isVisible: true,
        preventClearCart: true,
      });
    },
  },
  computed: {
    ...mapGetters([
      "deliveryData",
      "currDeliveryText",
      "getAvailableStoreShipmentsById",
      "userAddress",
      "getShipmentDataFromCart",
      "getCheckoutFetchingShipmentKey",
      "getSelectedShipmentByStoreId",
      "fulfillmentMethod",
    ]),
    fetchingShipmentsKey() {
      const { getCheckoutFetchingShipmentKey, storeId } = this;
      return getCheckoutFetchingShipmentKey(storeId);
    },
    defaultShipment() {
      const { choseCartShipment, shipments } = this;
      if (!shipments || !choseCartShipment) return null;
      return shipments.find(
        item => choseCartShipment.product_id === item.entity_id
      );
    },
    choseCartShipment() {
      const { getShipmentDataFromCart, storeId } = this;
      return getShipmentDataFromCart(storeId);
    },
    isSelectedAPickupMethod() {
      return get(this.choseCartShipment, "deal_address") === METHOD_PICKUP;
    },
    shipments() {
      const { storeId, getAvailableStoreShipmentsById } = this;
      return getAvailableStoreShipmentsById(storeId);
    },
  },
  async mounted() {
    const {
      storeId,
      syncSelectedShipmentWithCart,
      toggleLoading,
      fetchingShipmentsKey,
    } = this;
    this.isDataReady = false;
    toggleLoading({
      loadingKey: fetchingShipmentsKey,
      isLoading: true,
    });
    await syncSelectedShipmentWithCart({ storeId });
    toggleLoading({
      loadingKey: fetchingShipmentsKey,
      isLoading: false,
    });
    this.isDataReady = true;
  },
  updated() {
    if (this.isDataReady) {
      const selectedShipment = this.getSelectedShipmentByStoreId(this.storeId);
      if (
        this.fulfillmentMethod === FULFILLMENT_METHOD.PICKUP &&
        !selectedShipment &&
        this.shipments
      ) {
        const availablePickups =
          this.shipments &&
          this.shipments.filter(
            shipment => shipment.deal_address === METHOD_PICKUP
          );

        if (
          availablePickups.length === 1 &&
          availablePickups[0].has_options === 0
        ) {
          this.changeShipmentOption({
            target: { value: availablePickups[0].entity_id },
          });
        }
      }
    }
  },
  watch: {
    shipments(shipments) {
      const { choseCartShipment, removeShipmentInCart } = this;
      if (!choseCartShipment) return;
      const selectedShipment = shipments.find(
        shipment => shipment.entity_id === choseCartShipment.product_id
      );

      // if there is a shipment in cart but it not valid for now (not in delivery range, ...)
      // remove it
      if (!selectedShipment) {
        removeShipmentInCart({ shipmentId: choseCartShipment.product_id });
      }
    },
    choseCartShipment(choseCartShipment) {
      const selectedShipment = this.shipments.find(
        shipment => shipment.entity_id === choseCartShipment.product_id
      );

      const { storeId } = this;
      this.setSelectedShipmentOptionByStore({
        selectedOption: selectedShipment,
        storeId,
        clearAllStoreDeliveryOption: true,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.checkout-delivery {
  padding: 10px 0;
}
.checkout-delivery__title {
  margin: 20px 0 25px;
  padding-right: 20px;
  font-size: 18px;
}
.checkout-delivery__location {
  margin-left: -16px;
  cursor: pointer;
}
.delivery-section__item {
  min-height: 48px;
  display: flex;
  align-items: center;
}
.delivery-section__note {
  margin-top: 20px;
}
.delivery-section__list {
  margin-bottom: 10px;
}
.checkout-delivery__no-shipment {
  color: red;
  font-size: 16px;
  padding: 15px 0;
}
.checkout-delivery__location-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
</style>
