<template>
  <!-- Delivery section of the checkout page -->
  <div class="customer-info__wrapper">
    <div class="row customer-info-container" v-if="!isExpanded">
      <div class="customer-info__billing-header">
        <div class="customer-info-name col-xs-3">
          {{ addressName(userAddress) }}
        </div>
        <div class="customer-info-deliveryAddress col-xs-6">
          {{ addressText ? addressText : "" }}
        </div>
        <slot name="edit" @click.native="select" />
      </div>
      <div class="error-color col-xs-12" v-if="!isValidData">
        {{ $t("invalidBillingInfo") }}
      </div>
    </div>

    <div class="row customer-info-edit" v-else>
      <div class="customer-info-edit-header">{{ $t("headerText") }}</div>
      <div class="customer-info-elements">
        <div class="row">
          <div class="col-sm-6" v-for="(field, key) in newAddress" :key="key">
            <label class="label-text" :for="key">{{ $t(key) }}:</label>
            <input
              v-model="field.value"
              :id="key"
              :type="field.type"
              :autocomplete="field.autocomplete"
            />
            <span class="error-color" v-if="!field.value">{{
              $t("isRequired", { fieldName: field.label })
            }}</span>
          </div>
          <div class="col-sm-6 address">
            <label class="label-text">{{ $t("address") }}:</label>

            <div class="looks-like-input" @click="toggleChangeAddressModal">
              <LocationDisplay
                class="customer-info__location"
                noCheckMark
                :info="currDeliveryText"
              />
            </div>
            <div class="error-color mt--10" v-if="!isValidAddress">
              {{ $t("addressNotValid") }}
            </div>
          </div>
          <div class="col-sm-6">
            <label class="label-text">{{ $t("telephone") }}:</label>
            <input v-model="telephone" type="tel" autocomplete="tel-local" />
            <span class="error-color" v-if="!isValidPhoneNumber">{{
              !telephone
                ? $t("isRequired", { fieldName: "Telephone" })
                : $t("notValidPhoneNumber")
            }}</span>
          </div>
        </div>
        <div class="row pb--20">
          <div class="col-xs-6">
            <base-button
              :class="[
                'primary',
                'btn-block',
                'confirm-button',
                !isButtonActive && 'disabled',
              ]"
              @click.native="isButtonActive && collapse()"
              >{{ $t("continue") }}</base-button
            >
          </div>
          <div class="col-xs-6 not-your-account">
            {{ $t("notYourAccount") }}
            <router-link
              to="/checkout"
              @click.native="$store.dispatch('logout')"
              >{{ $t("logout") }}</router-link
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
