  },
  "en": {
    "newAddress": "New address",
    "continue": "Continue",
    "firstname": "First name",
    "lastname": "Last name",
    "address": "Address",
    "notYourAccount":"Not your account?",
    "headerText": "Billing info",
    "addressNotValid": "Address is not valid",
    "notValidPhoneNumber": "Phone number is not valid",
    "invalidBillingInfo": "Your billing info is not valid",
    "logout":"Log out"
  }
}
</i18n>

<script>
import putCheckout from "@/util/putCheckout";
import BaseButton from "@/components/BaseButton";
import LocationDisplay from "@/components/DeliveryModal/LocationDisplay";
import { mapGetters, mapActions } from "vuex";
import { MODAL_KEY_SELECT_DELIVERY_OPTION } from "@/store/modalModule";

export default {
  name: "CheckoutDelivery",
  components: {
    BaseButton,
    LocationDisplay,
  },
  props: {
    isExpanded: Boolean,
    telephone: String,
    expand: Function,
  },
  data() {
    const { firstname = "", lastname = "" } = this.userBilling || {};
    const newAddress = {
      firstname: {
        label: "First name",
        value: firstname,
        type: "text",
        autocomplete: "given-name",
      },
      lastname: {
        label: "Last name",
        value: lastname,
        type: "text",
        autocomplete: "family-name",
      },
    };
    return {
      newAddress,
      isButtonActive: false,
      timeoutId: null,
    };
  },
  methods: {
    ...mapActions(["toggleModal"]),
    activateButton() {
      this.isButtonActive = true;
    },
    deactivateButton() {
      this.isButtonActive = false;
    },
    collapse() {
      this.$emit("collapse");
    },
    addressName(address) {
      return address.firstname || "";
    },
    saveNewAddress() {
      this.$emit("activateButton");
      const {
        firstname,
        lastname,
        telephone,
        street,
        city,
        postcode,
      } = this.userAddress;
      clearTimeout(this.timeoutId);
      this.timeoutId = setTimeout(() => {
        putCheckout({
          address: {
            billing: {
              firstname,
              lastname,
              telephone,
              street: {
                "0": street,
              },
              city,
              country_id: "IS",
              amcustomerattr: {
                nationid: "0000000000",
              },
              postcode,
              use_for_shipping: 1,
            },
            billing_address_id: "",
          },
        });
      }, 500);
    },
    saveSelectedAddress() {
      this.$emit("activateButton");
      putCheckout({
        address: {
          billing: {
            use_for_shipping: 1,
          },
          billing_address_id: +this.userAddress.id,
        },
      });
    },
    mapAddress(address) {
      const {
        firstname,
        lastname,
        street,
        postcode,
        city,
        telephone,
        entity_id: address_id,
      } = address;
      return {
        firstname,
        lastname,
        street,
        postcode,
        city,
        telephone,
        address_id,
      };
    },
    saveAddressIfValid() {
      this.isValidData &&
        (this.isAddressNew
          ? this.saveNewAddress()
          : this.saveSelectedAddress());
    },
    toggleChangeAddressModal() {
      const { toggleModal } = this;
      toggleModal({
        modalKey: MODAL_KEY_SELECT_DELIVERY_OPTION,
        isVisible: true,
        preventClearCart: true,
      });
    },
    updateFirstNameLastName({ firstname, lastname }) {
      this.newAddress.firstname.value = firstname;
      this.newAddress.lastname.value = lastname;
    },
  },
  computed: {
    ...mapGetters({
      userAddressGetter: "userAddress",
    }),
    ...mapGetters(["currDeliveryText", "userBilling"]),
    addressText() {
      const { street, postcode, city, telephone } = this.userAddress || {};
      return (
        this.isValidData &&
        `
            ${street},
            ${postcode}
            ${city ? city : ""}
            (${telephone.replace("+354", "")})
          `
      );
    },
    isAddressNew() {
      return !this.userAddress.id;
    },
    isValidAddress() {
      const { street, postcode } = this.userAddress || {};
      return street && postcode && postcode.toString().length === 3;
    },
    isValidPhoneNumber() {
      const { telephone } = this.userAddress || {};
      return telephone && telephone.length >= 7;
    },
    isValidData() {
      const { firstname, lastname } = this.userAddress || {};
      return (
        firstname && lastname && this.isValidAddress && this.isValidPhoneNumber
      );
    },
    userAddress() {
      const {
        firstname: { value: firstname },
        lastname: { value: lastname },
      } = this.newAddress;
      return {
        ...this.userAddressGetter,
        ...{ firstname, lastname },
        telephone: this.telephone,
      };
    },
  },
  watch: {
    isValidData(isValid) {
      isValid
        ? this.activateButton()
        : this.deactivateButton() && this.expand();
    },
    userAddress() {
      this.saveAddressIfValid();
    },
    userAddressGetter({ firstname, lastname }) {
      if (firstname || lastname) {
        this.updateFirstNameLastName({ firstname, lastname });
      }
    },
    userBilling(userBilling) {
      this.updateFirstNameLastName(userBilling);
    },
  },
  mounted() {
    !this.isValidData ? this.expand() : this.activateButton();
    this.saveAddressIfValid();
    this.updateFirstNameLastName(this.userBilling);
  },
};
</script>

