<template>
  <div v-if="plasticBagOptionsAvailable" class="select-plastic-bag">
    <div class="select-pastic-bag__select-wraper icon icon--shopping-basket">
      <select @change="changePlasticBagOption">
        <option
          :selected="selectedPlasticBagOption.value === undefined"
          disabled
          hidden
        >
          {{ $t("choosePackagingMethod") }}
        </option>
        <option
          v-for="option in plasticBagOptions"
          :key="option.value"
          :selected="option.value === selectedPlasticBagOption.value"
          :value="option.value"
          >{{ option.label }}</option
        >
      </select>

      <i class="select-pastic-bag__arrow-down-icon icon icon--arrow-down" />
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "choosePackagingMethod": "Veldu pökkunarmáta"
  },
  "en": {
    "choosePackagingMethod": "Choose your packaging method"
  }
}
</i18n>

<script>
import { mapGetters, mapActions } from "vuex";
import getShipmentTextByLanguage from "@/util/getShipmentTextByLanguage";
import { ERROR_KEY_CHECKOUT_SCREEN } from "@/store/errorsModule";
import get from "@/util/get";

export default {
  name: "SelectPlasticBagOption",
  props: {
    storeId: Number,
  },
  computed: {
    ...mapGetters(["getStorePlasticBagOptions", "getCheckoutErrorByStore"]),
    storePlasticBagOptions() {
      const { getStorePlasticBagOptions, storeId } = this;
      return getStorePlasticBagOptions(storeId) || [];
    },
    selectedPlasticBagOption() {
      return {};
    },
    plasticBagOptions() {
      const { storePlasticBagOptions } = this;
      return storePlasticBagOptions.map(option => ({
        value: option.entity_id,
        label: getShipmentTextByLanguage(option),
      }));
    },
    errors() {
      return this.getCheckoutErrorByStore(this.storeId);
    },
    plasticBagOptionsAvailable() {
      const { storePlasticBagOptions } = this;
      return get(storePlasticBagOptions, "0.is_in_stock");
    },
  },
  methods: {
    ...mapActions(["addPlasticBagToCart", "setError"]),
    changePlasticBagOption(e) {
      const value = +e.target.value;
      const {
        addPlasticBagToCart,
        storeId,
        storePlasticBagOptions,
        setError,
      } = this;
      const selectedPlasticBagOption = storePlasticBagOptions.find(
        option => option.entity_id === value
      );
      addPlasticBagToCart({
        storeId,
        plasticBag: selectedPlasticBagOption,
      });
      setError({ errorKey: ERROR_KEY_CHECKOUT_SCREEN });
    },
  },
  beforeDestroy() {
    this.setError({ errorKey: ERROR_KEY_CHECKOUT_SCREEN });
  },
};
</script>

