<template>
  <!-- Review section of the Checkout page -->
  <div class="checkout-box">
    <div class="order-info-with-indicator" @click="toggle(storeCart.name)">
      <i
        :class="[
          `icon icon--arrow-${
            isStoreExpanded(storeCart.name) ? 'down' : 'right'
          } order-info__indicator`,
        ]"
      />
      <div class="order-info-container">
        <div class="order-info-name">
          {{ isDeals ? $t("deals") : storeCart.name }}
          <LastOderTimeWarning :id="storeCart.id" />
          <div
            v-if="!hasAddressForDeal(storeCart)"
            class="order-info-delivery order-info-delivery--missing-method"
          >
            {{ $t("dealAddressRequired") }}
          </div>
          <div
            class="order-info__error-list"
            v-if="errors(storeCart.id).length"
          >
            <div
              v-for="errorName in errors(storeCart.id)"
              :key="errorName"
              class="order-info-delivery order-info-delivery--missing-method"
            >
              {{ $t(errorName) }}
            </div>
          </div>
        </div>
        <div class="order-info-total">
          {{ storeCart.priceTotal | formatISK }}
        </div>
        <div class="order-info-trash-btn" @click.stop="clearStoreFromCart(id)">
          <i class="icon icon--trash" />
        </div>
      </div>
    </div>
    <div
      class="row order-item-container"
      v-show="isStoreExpanded(storeCart.name)"
      v-for="(item, i) in storeCart.items"
      :key="i"
    >
      <div class="item-image col-xs-2 col-sm-1">
        <ImageViewer
          :src="productCartImage(item)"
          class="img img-responsive"
          loading="lazy"
          :alt="item.name"
        />
      </div>
      <div class="order-item-description col-xs-5 col-sm-6">
        {{ item.name }}
      </div>
      <div class="order-item-unitPrice col-xs-2 col-sm-2">
        {{ item | unitPrice | formatISK }}
      </div>
      <div class="order-item-qty col-xs-1 col-sm-1">{{ item.cart_qty }}</div>
      <div class="order-item-priceTotal col-xs-2 col-sm-2">
        {{ item | priceTotal | formatISK }}
      </div>
    </div>
    <div class="row delivery-option-wrapper" v-if="!dontNeedDeliveryInfo">
      <div class="col-xs-12">
        <CheckoutDeliveryInfo
          :showNoteToSeller="!isDeals"
          :storeId="storeCart.id"
        />
      </div>
    </div>
  </div>
</template>

