    "unitPrice": "Unit Price",
    "price": "Price",
    "total": "Total",
    "confirmAndPay": "Confirm & Pay",
    "processingPayment": "Processing payment...",
    "deals": "Deals",
    "shipment_required": "A delivery method is required",
    "dealAddressRequired": "Your address is required for those deal products",
    "plastic_bags_required": "Packaging method is required"
  }
}
</i18n>

<script>
import ImageViewer from "@/components/ImageViewer";
import CheckoutDeliveryInfo from "@/components/CheckoutDeliveryInfo";
import LastOderTimeWarning from "@/components/LastOderTimeWarning";
import { mapActions, mapGetters } from "vuex";
import merchant from "@/mixins/merchant";
import clearStoreFromCart from "@/mixins/clearStoreFromCart";
import cartItemsByStore from "@/mixins/cartItemsByStore";
import get from "@/util/get";
import { FULFILLMENT_METHOD, ATTRIBUTE_SET } from "@/util/constants";

export default {
  name: "CheckoutReviewStore",
  props: {
    storeCart: Object,
    id: Number, // storeId
  },
  components: {
    CheckoutDeliveryInfo,
    ImageViewer,
    LastOderTimeWarning,
  },
  mixins: [merchant, cartItemsByStore, clearStoreFromCart],
  data() {
    return {
      isExpanded: {},
    };
  },
  computed: {
    ...mapGetters(["isMissingAddressData", "getCheckoutErrorByStore"]),
    dontNeedDeliveryInfo() {
      // every items is deal with pickup method
      return get(this.storeCart, "items", []).every(
        cartItem =>
          cartItem.attribute_set_id === ATTRIBUTE_SET.DAILY_DEALS &&
          cartItem.deal_address === FULFILLMENT_METHOD.PICKUP
      );
    },
    isDeals() {
      return !this.storeCart.name;
    },
  },
  methods: {
    ...mapActions(["toggleModal", "fetchStoreShipmentsById"]),
    isStoreExpanded(store) {
      return !!this.isExpanded[store];
    },
    toggle(store) {
      this.$set(this.isExpanded, store, !this.isStoreExpanded(store));
    },
    productCartImage(item) {
      return (
        "https://images.aha.is/image/upload/f_auto,h_60/products/" +
        item.product_img
      );
    },
    hasAddressForDeal(store) {
      const { id: storeId } = store;
      // it's not a deal
      if (storeId) {
        this.$emit("activateButton");
        return true;
      }

      const { isMissingAddressData } = this;
      // not have address and
      if (
        store.items.some(item => item.deal_address === 1) &&
        isMissingAddressData
      ) {
        this.$emit("deactivateButton");
        return false;
      }
      this.$emit("activateButton");
      return true;
    },
    errors(storeId) {
      const errorLst = this.getCheckoutErrorByStore(storeId);
      return Object.values(errorLst || {});
    },
  },
  filters: {
    unitPrice(item) {
      console.log("unitPrice -> item", item);
      return item.price_incl_tax - (+item.discount_amount || 0) / item.cart_qty;
    },
    priceTotal(item) {
      return item.cart_qty * item.price_incl_tax - (+item.discount_amount || 0);
    },
  },
};
</script>

